.iucp-address-container {
  form {
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: auto;

    #iucp_submit_address_button {
      display: none;
      align-self: center;
    }
    #iucp-label-submit {
      align-self: center;
      margin-top: 2rem;
      font-size: 1.5rem;
      padding: 1rem;
      background-color: lightblue;
      border-radius: 50rem;
      cursor: pointer;
    }
  }
}
